import React, { useEffect, useState, useRef } from 'react';
import * as d3 from 'd3';
import * as topojson from 'topojson-client'; // Import the topojson library
import worldMapData from '../../../../countries-50m-ver.json'; // Use the new data file
import './WorldMap.css';


function find_max_value(data, year) {
    let max_v = 0;
    for (let i = 0; i < data.length; i++) {
        max_v = Math.max(data[i][year], max_v)
    }
    return max_v;
}

function WorldMap({ alldata, content, year, setChosenCounrty}) { 
    const [scale, setScale] = useState(100);
    const updateMapRef = useRef(null);

    useEffect(() => {
        const contentData = alldata[content];
        const max_value = find_max_value(contentData, year);

        const data = contentData.reduce((acc, countryData) => {
            acc[countryData.Country] = countryData;
            return acc;
        }, {}); 

        
        const svg = d3.select("#world-map");
        svg.selectAll("*").remove(); // Clear the SVG content

        // Convert the TopoJSON into GeoJSON
        const geojson = topojson.feature(worldMapData, worldMapData.objects.countries);
        
        // Define the projection and path generator
        const projection = d3.geoMercator()
            .scale(scale)
            .translate([400, 300]);
        const path = d3.geoPath().projection(projection);

        // Define a color scale
        const colorScale = d3.scaleSequential()
            .domain([0, max_value]) // Adjust this domain based on your data
            .interpolator(d3.interpolateBlues); // interpolateBlues 蓝色 interpolateSpectral 多种颜色

        // Create a tooltip div that is hidden by default
        const tooltip = d3.select("body").append("div")
            .attr("class", "tooltip")
            .style("opacity", 0);

        function updateMap(newScale) {
            projection.scale(newScale);
            svg.selectAll("path").attr("d", path);
        }

        updateMapRef.current = updateMap;


        // 获取初始的 translate 和 scale 值
        const initialTranslate = projection.translate();
        const initialScale = projection.scale();

        // 定义缩放和拖拽行为
        const zoom = d3.zoom()
            .scaleExtent([1, 10])  // 设置缩放范围
            .on('zoom', (event) => {
                const { transform } = event;
                projection
                    .translate([
                        initialTranslate[0] + transform.x,
                        initialTranslate[1] + transform.y
                    ])
                    .scale(initialScale * transform.k);
                svg.selectAll("path")
                    .attr("d", path);
            });
        svg.call(zoom);


        // Create the color bar
        let colorBarSvg = d3.select(".map-container").select(".color-bar");
        if (colorBarSvg.empty()) {
            colorBarSvg = d3.select(".map-container").append("svg")
                .attr("class", "color-bar")
                .attr("width", 100)
                .attr("height", 400);
        } else {
            colorBarSvg.selectAll("*").remove(); // Remove all children of the color bar
        }

        const gradient = colorBarSvg.append("defs")
            .append("linearGradient")
            .attr("id", "gradient")
            .attr("x1", "100%")
            .attr("y1", "100%");
        
        gradient.append("stop")
        .attr("offset", "0%")
        .attr("stop-color", d3.interpolateBlues(0));
    
        gradient.append("stop")
            .attr("offset", "100%")
            .attr("stop-color", d3.interpolateBlues(1));
        
        colorBarSvg.append("rect")
            .attr("width", 40)
            .attr("height", 300)
            .style("fill", "url(#gradient)");

        
        // Add scale to the color bar
        const yScale = d3.scaleLinear()
            .range([300, 0])
            .domain([0, max_value]); // Adjust this domain based on your data

        const yAxis = d3.axisRight(yScale);
        
        colorBarSvg.append("g")
            .attr("class", "y-axis") // Add class to the axis for future selection
            .attr("transform", "translate(50, 0)")
            .call(yAxis);


        // Draw the GeoJSON map
        svg.selectAll("path")
            .data(geojson.features) // Use the converted GeoJSON data
            .enter().append("path")
            .attr("d", path)
            .attr("fill", d => {
                const countryName = d.properties.name; // Adjust based on your new GeoJSON
                const countryData = data[countryName];
                const value = countryData ? countryData[year] : null;
                return value ? colorScale(value) : 'white';
            })
            .attr("stroke", "black")
            .on("mouseover", (event, d) => {
                const countryName = d.properties.name;
                const countryData = data[countryName];
                const value = countryData ? countryData[year] : "No data";
                
                tooltip.transition()
                    .duration(200)
                    .style("opacity", .9);
                tooltip.html(`${countryName}: ${typeof value === 'number' ? value.toFixed(0) : 'No data'}`)
                    .style("left", (event.pageX) + "px")
                    .style("top", (event.pageY - 28) + "px");
            })
            .on("mouseout", (event, d) => {
                tooltip.transition()
                    .duration(500)
                    .style("opacity", 0);
            })
            .on("click", (event, d) => {
                const countryName = d.properties.name;
                // console.log(countryName); // Output the country name to the console
                setChosenCounrty(countryName);
            });
    }, [alldata, content, year, scale]);

    return (
    <div className="map-container">
        <svg id="world-map"></svg>
        <div className="zoom-controls">
            <button 
                id="zoom-in" 
                className="zoom-button"
                onClick={() => {
                    const newScale = scale + 10;
                    setScale(newScale);
                    updateMapRef.current(newScale);
                }}
            >+</button>
            <button 
                id="zoom-out" 
                className="zoom-button"
                onClick={() => {setScale(scale - 10);}}
            >-</button>
        </div>
    </div>
        
    )
    
}

export default WorldMap;
