import React from 'react';
import { DownOutlined } from '@ant-design/icons';
import { Dropdown, Space } from 'antd';

const items = [
    {
        label: 'GHG emissions from Energy (total)',
        key: '0'
    },
    {
        label: 'GHG fugitive emissions (total)',
        key: '1'
    },
    {
        label: 'GHG emissions from fuel combustion (total)',
        key: '2'
    },
    {
        label: 'GHG emissions from fuel combustion (Coal)',
        key: '3'
    },
    {
        label: 'GHG emissions from fuel combustion (Oil)',
        key: '4'
    },
    {
        label: 'GHG emissions from fuel combustion (Gas)',
        key: '5'
    },
    {
        label: 'CO2 emissions from international (Marine bunkers)',
        key: '6'
    },
    {
        label: 'CO2 emissions from international (aviation bunkers)',
        key: '7'
    },
    {
        label: 'CO2 emissions/TES',
        key: '8'
    },
    {
        label: 'CO2 emissions/GDP using exchange rates',
        key: '9'
    },
    {
        label: 'CO2 emissions/GDP using purchasing power parities',
        key: '10'
    },
    {
        label: 'CO2 emissions/population',
        key: '11'
    },
];

const DownList = ({content, setContent}) => {
    const onClick =  ({key}) => {
        setContent(items[parseInt(key, 10)].label);
    } 
    return (
        <Dropdown
            menu={{
            items,
            onClick,
            }}
            trigger={['click']}
        >
            <a onClick={(e) => e.preventDefault()}>
            <Space>
                <h1>{content}</h1>
                <DownOutlined />
            </Space>
            </a>
        </Dropdown>
    )
};
export default DownList;